import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  hardPoint: 0, // .    
  partyPoint: 0, //+ .   + 
  fightPoint: 0, // .    
};

export const pointSlice = createSlice({
  name: "point",
  initialState,
  reducers: {
    changeHardPoint: (state, action) => {
      state.hardPoint += action.payload;
    },
    changePartyPoint: (state, action) => {
      state.partyPoint += action.payload;
    },
    changeFightPoint: (state, action) => {
      state.fightPoint += action.payload;
    },
    resetPoint: (state, action) => {
      state.hardPoint = 0;
      state.partyPoint = 0;
      state.fightPoint = 0;
    },
  },
});

// Action creators are generated for each case reducer function
export const {
  changeHardPoint,
  changePartyPoint,
  changeFightPoint,
  resetPoint,
} = pointSlice.actions;

export default pointSlice.reducer;
